body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
* {
  color: #303030; }

.list-container {
  position: relative;
  display: inline-flex;
  width: 350px;
  padding: 1rem;
  border: 2px solid #303030;
  margin-top: 3rem;
  box-shadow: 2px 2px #707070; }
  .list-container li .edit-button {
    width: 0;
    overflow: hidden;
    transition: 100ms;
    position: absolute;
    right: 2rem;
    padding-top: 5px;
    font-size: 1.5rem; }
  .list-container li .name {
    width: 250px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    display: inline-block;
    transition: 100ms; }
    .list-container li .name.active {
      font-weight: 600; }
    .list-container li .name:hover {
      -webkit-transform: scale(1.1);
              transform: scale(1.1); }
  .list-container li:hover .edit-button {
    width: 1.6rem; }
    .list-container li:hover .edit-button:hover {
      -webkit-transform: scale(1.3);
              transform: scale(1.3); }
  .list-container li.add-button:hover {
    transition: 100ms;
    -webkit-transform: scale(1.3);
            transform: scale(1.3); }
  .list-container.category-list {
    z-index: 2;
    margin-left: 2rem;
    min-height: 40vh;
    border-radius: 5px;
    background-color: white; }
  .list-container.card-list {
    z-index: 1;
    transition: 300ms ease-out;
    left: -18rem;
    width: 15rem;
    top: 1rem;
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-bottom-left-radius: 5px; }
    .list-container.card-list.show {
      left: 0;
      width: 350px; }
  .list-container ul {
    width: 100%;
    padding: 1rem; }
    .list-container ul li {
      list-style-type: none;
      font-size: 2rem;
      cursor: pointer;
      text-align: center; }

.card-container {
  display: inline-flex;
  flex-direction: column;
  position: relative;
  top: 4rem;
  left: 1rem;
  border: 2px solid #303030;
  border-radius: 5px;
  width: 800px;
  padding: 2rem 1rem 2rem 2rem;
  box-shadow: 2px 2px #707070; }
  .card-container header {
    display: flex;
    justify-content: flex-end;
    align-items: center; }
  .card-container .card-section-header {
    display: block;
    margin-left: 5%;
    padding: 1rem 1rem 0 1rem;
    font-style: italic;
    text-align: left;
    width: 100%; }
  .card-container .card-name {
    display: inline-block;
    width: 90%;
    text-align: center;
    font-size: 2.5rem;
    align-self: center;
    margin-left: auto;
    margin-right: auto; }

.editor-container {
  width: 100%;
  display: flex;
  align-items: center;
  flex-wrap: nowrap;
  white-space: nowrap;
  align-items: center; }
  .editor-container .editor {
    cursor: text;
    padding: 3px 8px;
    white-space: normal;
    overflow-wrap: break-word;
    display: inline-block;
    width: 85%;
    margin-left: 5%;
    transition: 500ms; }
    .editor-container .editor:not(.focus):hover {
      box-shadow: 0 0 10px #707070; }
  .editor-container .focus {
    border-radius: 5px;
    box-shadow: 0 0 10px #303030; }
    .editor-container .focus:focus {
      outline-color: #303030; }

.cancel-confirm {
  margin-left: 0.5rem;
  font-size: 1.4rem;
  cursor: pointer;
  white-space: nowrap;
  display: inline-block;
  visibility: hidden;
  opacity: 0;
  transition: opacity 500ms; }
  .cancel-confirm .button.cancel-button {
    margin-left: 0.5rem; }
  .cancel-confirm .button.confirm-button {
    margin-left: 0.2rem; }
  .cancel-confirm svg:hover {
    transition: 100ms;
    -webkit-transform: scale(1.3);
            transform: scale(1.3); }
  .cancel-confirm.show {
    visibility: visible;
    opacity: 1;
    transition: opacity 500ms; }

